.wrapper {
    /*position: absolute;*/
    top: 0;
    left: 0;
    position: relative;
}

.pStatus {
    padding-top: 5px;
    padding-bottom: 5px;
    position: relative;
}
.pStatusBox {
}
.statusUpdating {
}
.pStatusBox .statusUpdating {
    position: absolute;
    top: -12px;
    left: 80px;
    font-size: 12px;
    padding: 3px;
    opacity: 0.7;
    display: flex;
    align-items: center;
    background: #ecb0b0;
}

.pStatus:hover {
    background: rgba(194, 194, 194, 0.38);
}
.inputWrap {
    min-width: 250px;
    background: #f0f2f5;
    padding: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.55);
    position: absolute;
    top: 0;
    left: 0;
}
.input {
    width: 100%;
    padding: 3px 5px;
    margin-bottom: 7px;
    border: 1px solid rgba(128, 128, 128, 0.42);
}

.btnSaveStatus {
    padding: 4px 8px;
    background: rgba(160, 225, 111, 0.77);
    margin-right: 10px;
}
.btnCloseStatus {
    padding: 4px 8px;
    background: rgba(225, 111, 111, 0.77);
}
@media screen and (max-width: 576px) {
    .inputWrap {
        min-width: 250px;
        max-width: 260px;
        background: #f0f2f5;
        padding: 10px;
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.55);
        position: fixed;
        top: 70px;
        left: 50%;
        transform: translateX(-50%);
        z-index: 11;
    }
}
