	top: calc(50% - 10px);
	left: 25%;
	left: calc(50% - 75px);

	background-color: #4f4b9975;
}

.PreloaderLine_loader__pkIYe:before{
	content: "";
	position: absolute;
	background-color: #4f4b99;
	top: 0px;
	left: 0px;
	height: 20px;
	width: 0px;
	z-index: 0;
	opacity: 1;
	-webkit-transform-origin:  100% 0%;
			transform-origin:  100% 0% ;
	-webkit-animation: PreloaderLine_loader__pkIYe 6s ease-in-out infinite;
			animation: PreloaderLine_loader__pkIYe 6s ease-in-out infinite;
}

.PreloaderLine_loader__pkIYe:after{
	content: "LOADING";
	color: #fff;
	/* font-family:  Lato,"Helvetica Neue" ; */
	font-weight: 400;
  letter-spacing: 1px;
	font-size: 16px;
	position: absolute;
	/* width: 100%; */
	height: 20px;
	line-height: 20px;
	left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
	top: 0;
}

@-webkit-keyframes PreloaderLine_loader__pkIYe{
    0%{width: 0px;}
    70%{width: 100%; opacity: 1;}
    90%{opacity: 0; width: 100%;}
