
.messageBox {
    display: grid;
    grid-template-columns: 100%;
}
.dialogWrapper {
  display: flex;
  flex-direction: column;
  max-height: 100%;
  justify-content: space-between;
}
.messagesWrapper {
    flex: 1 1 auto;
    
}
.inputTextMessage {

    width: 100%;
    padding: 0px 0px 10px 0px;
}
.myMessage {
}
.bodyMessage {
    padding: 5px 7px;
    margin-bottom: 10px;
    max-width: 80%;
}
.notMyMessage {
    background-color: rgba(141, 155, 141, 0.296);
    justify-self: start;
}
.myMessage {
    justify-self: end;
    background-color: rgba(0, 0, 255, 0.241);
}
.line {
  width: 100%;
  height: 1px;
  background-color: rgba(128, 128, 128, 0.378);
  margin: 25px 0px 10px 0px;
}
.wrapperBtnLine{
position: relative;
}
.wrappBtn {
  position: fixed;
  top: 40px;
  left: 0;
  background-color: #8583a79a;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.400);
  width: 100%;
  padding: 10px 0;
}
.wrappBtn {
  display: flex;
  justify-content: center;
}